.react-select {
  div &__menu, div &__control {
    max-width: 345px;
    background-color: #141a2c;
    border: 1px solid #323B54;
    color: white;
    &:focus {
      outline: none;
      border: none;
    }

    &:hover {
      background-color: #323B54;
    }
  }
}

.react-select__placeholder, .react-select__single-value {
  color: #fff!important;
}
